<template>
    <v-card outlined>
        <v-card-text>
            <div class="text-center" style="padding-bottom: 10px">
                <p class="text--primary" style="font-size: 20px" >** {{selectedMode.toUpperCase()}} **</p>
            </div>
            <v-row class="text-center" style="min-height:175px">
                <v-col cols="12">
                    <p class="text--primary" v-if="hasUserAnswered" style="font-size: 60px">
                        {{answer.hiragana}}
                    </p>
                </v-col>
                <v-col cols="12">
                    <p v-if="hasUserAnswered" style="font-size: 25px">
                        {{answer.kanji}}
                    </p>
                    <v-chip
                            class="ma-2"
                            color="green"
                            text-color="white"
                            v-if="hasUserAnswered && isUserAnswerCorrect"
                    >
                        <v-avatar left>
                            <v-icon>mdi-checkbox-marked-circle</v-icon>
                        </v-avatar>
                        Correct
                    </v-chip>
                    <v-chip
                            class="ma-2"
                            color="red"
                            text-color="white"
                            v-if="hasUserAnswered && !isUserAnswerCorrect"
                    >
                        <v-avatar left>
                            <v-icon>mdi-checkbox-marked-circle</v-icon>
                        </v-avatar>
                        Wrong
                    </v-chip>
                </v-col>
            </v-row>
            <div class="text-center">
                <v-btn
                        ref="voiceBtn"
                        :loading="hasRecognitionStarted"
                        id="voiceBtn"
                        class="align-center"
                        fab
                        color="red"
                        :disabled="hasRecognitionStarted"
                        ripple
                        @click="startRecognition"
                >
                    <v-icon>settings_voice</v-icon>
                </v-btn>
            </div>

        </v-card-text>
    </v-card>
</template>

<script>

    import {conjugate} from '@/libs/jpConjugation';

    function say(word) {
        const msg = new SpeechSynthesisUtterance();
        msg.volume = 1;
        msg.rate = 0.5;
        msg.pitch = 0.8;
        msg.text = word;
        msg.lang = 'ja-JP';
        speechSynthesis.speak(msg);
    }


    export default {
        name: "AnswerCard",
        data: () => ({
            hasUserAnswered: false,
            isUserAnswerCorrect: false,
            hasRecognitionStarted: false,
            answer: {},
            selectedMode: '',
        }),
        props: {
            verb: {
                type: Object,
                required: true,
            }
        },
        methods: {
            startRecognition: function () {
                this.hasRecognitionStarted = true;
                this.recognition.start();
            },
            stopRecognition: function () {
                this.hasRecognitionStarted = false;
                this.recognition.stop();
            },
            restoreDefaultState: function () {
                this.hasUserAnswered = false;
                this.hasRecognitionStarted = false;
            },
            checkApi: function () {
                const recognition = new window.webkitSpeechRecognition() || new window.SpeechRecognition();
                if (!recognition) {
                    this.$emit('speechApiError')
                }
                const speechRecognitionList = new window.webkitSpeechGrammarList();
                const conjugation = [this.answer.kanji, this.answer.hiragana];
                const grammar = `#JSGF V1.0; grammar conjugation; public <conjugation> = ${conjugation.join(' | ')};`;
                speechRecognitionList.addFromString(grammar, 20);
                recognition.grammars = speechRecognitionList;
                recognition.continuous = false;
                recognition.lang = 'ja-JP';
                recognition.interimResults = false;
                recognition.maxAlternatives = 10;
                recognition.onresult = (event) => {
                    const results = Array.from(event.results[0]);
                    //console.log(results);
                    const findAnswer = results
                        .find(r => r.transcript === this.answer.kanji || r.transcript === this.answer.hiragana);
                    this.isUserAnswerCorrect = findAnswer != null;
                    this.hasUserAnswered = true;
                    this.hasRecognitionStarted = false;
                    recognition.stop();
                    say(this.answer.hiragana);
                    // Go to the next verb question
                    setTimeout(() => {
                        this.restoreDefaultState();
                        this.$emit('nextVerb');

                    }, 3000);
                    if (this.isAutoModeEnabled) {
                        setTimeout(() => {
                            this.$refs.voiceBtn.click();
                        }, 4500);
                    }

                }
                recognition.onend = () => {
                    if (!this.hasUserAnswered) {
                        this.restoreDefaultState();
                    }
                }
                this.recognition = recognition;
            },
        },
        mounted() {
            this.checkApi();
            // Activate recognition when space bar is clicked
            window.addEventListener("keypress", e => {
                if (e.code === 'Space') {
                    if (this.hasRecognitionStarted) {
                        this.stopRecognition();
                    } else {
                        this.startRecognition();
                    }
                }
            });
        },
        computed: {
            selectedModes() {
                return [...this.$store.state.selectedModes];
            },
            isAutoModeEnabled() {
                return this.$store.state.isAutoModeEnabled;
            }
        },
        watch: {
            verb: {
                immediate: true,
                deep: true,
                handler(currentVerb) {
                    // Calculate the correct answer based on the verb
                    const selectedModes = this.selectedModes;
                    const selectedRandomMode = selectedModes[Math.floor(Math.random() * Math.floor(selectedModes.length))]
                    this.selectedMode = selectedRandomMode;
                    this.answer.kanji = conjugate(currentVerb.dict, this.verb.type).find(it => it.name === selectedRandomMode).form;
                    this.answer.hiragana = conjugate(currentVerb.hiragana, this.verb.type).find(it => it.name === selectedRandomMode).form;
                    say(currentVerb.hiragana);
                    //console.log(`Mode: ${selectedRandomMode} - Answer: ${this.answer.hiragana}  -  ${this.answer.kanji}`);
                }
            },
           /* selectedModes: {
                handler(modes) {
                    console.log(modes);
                }
            }*/
        }
    }
</script>
