<template>
    <v-app dark>

        <!-- Start navigation drawer -->
        <v-navigation-drawer v-model="drawer" app clipped>
            <v-list dense>
                <v-list-item v-for="(mode, index) in modes" :key="index">
                    <v-switch
                            dark
                            inset
                            :disabled="isLastOneMode(mode)"
                            color="purple"
                            v-model="selectedModes"
                            :label="mode.toUpperCase()"
                            :value="mode"
                    />
                </v-list-item>
            </v-list>
        </v-navigation-drawer>
        <!-- End navigation drawer -->

        <!-- Start app bar -->
        <v-app-bar id="app-bar" app clipped-left>
            <v-app-bar-nav-icon @click.stop="drawer = !drawer"/>
            <v-spacer/>
            <p style="padding-top: 0.5%; padding-right: 1%; color: darkgray">Enable Auto Recognition</p>
            <v-switch style="padding-top: 1%" class="text-right" color="green" v-model="isAutoModeEnabled"/>
        </v-app-bar>
        <!-- End app bar -->

        <!-- Start main content -->
        <v-content>
            <!-- Dialog error if web speech api is not available -->
            <v-dialog light v-model="isNotChrome" max-width="400">
                <v-card>
                    <v-card-title>Use Google Chrome</v-card-title>
                    <v-card-text>This webapp is only supported in Google Chrome.</v-card-text>
                </v-card>
            </v-dialog>

            <VerbQuiz :verb='currentVerb' @nextVerb='nextVerb'></VerbQuiz>
        </v-content>
        <!-- End main content -->

        <!-- Start footer -->
        <v-footer absolute app>
            <div  style="width: 20%; font-size: 14px" >
                <span>Press the space bar to start the speech recognition</span>
            </div>
            <div style="width: 80%; font-size: 14px" class="text-lg-right">
                <span style="color: gray; padding-right: 15px">
                    Buy me a beer if you like it!
                </span>
                <span style="color: gray; padding-right: 15px">
                    BTC: 1Ah2FR1WdR854eKjF8ijQjm3VSGeragq9K
                </span>
                <span style="color: gray">
                    ETH: 0xe41667a78e4c656ea2860ee65df6da866e86d446
                </span>
            </div>
        </v-footer>
        <!-- End footer  -->

    </v-app>
</template>

<script>
    import {mapActions} from 'vuex';
    import VerbQuiz from '@/components/VerbQuiz';
    import verbs from './libs/jpVerbs';

    export default {
        name: 'App',
        components: {
            VerbQuiz,
        },
        data: () => ({
            currentVerb: undefined,
            drawer: null,
            isNotChrome: false,
            currentIndexVerb: 0,
            modes: ['present polite', 'present polite negative', 'present negative',
                'past', 'past polite', 'past negative', 'past polite negative',
                'desire', 'potential', 'te form', 'passive', 'causative'],
        }),
        computed: {
            selectedModes: {
                get: function () {
                    return [...this.$store.state.selectedModes];
                },
                set: function (modes) {
                    // this.$store.dispatch('addSelectedModeAction', mode)
                    // because we are using ...mapActions
                    if (modes.length === 0) return;
                    this.updateSelectedModeAction(modes);
                },
            },
            isAutoModeEnabled: {
                get: function () {
                    return this.$store.state.isAutoModeEnabled;
                },
                set: function (isEnabled) {
                    this.setAutoModeAction(isEnabled);
                }
            }
        },
        methods: {
            nextVerb() {
                if (this.currentIndexVerb < verbs.length) {
                    this.currentVerb = verbs[this.currentIndexVerb];
                    this.currentIndexVerb++;
                }
            },
            isLastOneMode(mode) {
                return this.selectedModes.length === 1 && mode === this.selectedModes[0];
            },
            ...mapActions(['addSelectedModeAction', 'updateSelectedModeAction', 'setAutoModeAction'])
        },
        created() {
            this.nextVerb();
            this.$vuetify.theme.dark = true;
        },
        mounted() {
            this.isNotChrome = !window.chrome;
        }
    };
</script>

<style type='text/css' scoped>
    #app-bar {
        background-image: url('./assets/app-bar-texture.jpg');
        background-repeat: repeat-x;
    }
</style>
