<template>
        <v-container
                id="v-container"
                class="fill-height"
                fluid>
            <div id="background"/>

            <!-- Dialog error if web speech api is not available -->
            <v-dialog v-model="showErrorDialog" max-width="400">
                <v-card>
                    <v-card-title>Use Chrome</v-card-title>
                    <v-card-text>Speech Recognition does not exist on this browser.</v-card-text>
                </v-card>
            </v-dialog>

            <!--Quiz -->
            <v-row style="z-index: 2">
                <v-col sm="12" md="6" offset-md="3" lg="6" offset-lg="3" >
                    <!-- Question card -->
                    <QuestionCard
                            id="question-card"
                            :verb="verb"/>

                    <!-- Answer card -->
                    <AnswerCard
                            id="answer-card"
                            :verb="verb"
                            @nextVerb="$emit('nextVerb')"
                            @speechApiError="speechApiError"/>
                </v-col>
            </v-row>

        </v-container>
</template>

<script>

    import QuestionCard from "@/components/QuestionCard";
    import AnswerCard from "@/components/AnswerCard";

    export default {
        name: "VerbQuiz",
        components: {
            QuestionCard,
            AnswerCard,
        },
        data: () => ({
            showErrorDialog: false,
        }),
        props: {
            verb: {
                type: Object,
                required: true,
            },
        },
        methods: {
            speechApiError: function () {
                this.showErrorDialog = true;
            }
        },
    }
</script>

<style type="text/css" scoped>
    #background {
        background-image: url("../assets/bg.jpg");
        background-size: cover;
        background-blend-mode: darken;
        height: 100%;
        width: 100%;
        z-index: 1;
        position: fixed;
        opacity: 0.1;
        float:left;

    }

    #question-card {
        margin-bottom: 5vh;
    }
</style>
